import Vue from "vue";
import VueRouter from "vue-router";
import LandingPage from "../views/landing-page.vue";
import Game from "../views/game.vue";


Vue.use(VueRouter);

Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "LandingPage",
    component: LandingPage
  },
  {
    path: "/game/:judge",
    name: "Game",
    component: Game
  }
];

const router = new VueRouter({
  mode: "history",
  base: "/test-deploy-vue",
  routes
});

export default router;
