<template>
  <div>
    <Nav
      :judge="judge"
      @change-judge="changeJudge"
    />
    <Relateds v-if="judge == 'relateds'" />
    <Exhibits v-if="judge == 'exhibits'" />
    <Souls v-if="judge == 'souls'" />
    <Times v-if="judge == 'times'" />
  </div>
</template>
<script src="https://static.line-scdn.net/liff/edge/2/sdk.js"></script>
<script>
import Vue from "vue";
import Nav from "@/components/game/nav.vue";
import Relateds from "@/components/game/relateds.vue";
import Exhibits from "@/components/game/exhibits.vue";
import Souls from "@/components/game/souls.vue"
import Times from "@/components/game/times.vue"

export default {
  components: {
    Nav,
    Relateds,
    Exhibits,
    Souls,
    Times
  },
  data() {
    return {
      judge: this.$route.params.judge,
      code: ""
    };
  },
  mounted(){
    if ( this.$route.params.judge == "relateds" ){
      this.code = "1653951718-QO7MJ5eD"
    } else if ( this.$route.params.judge == "exhibits" ){
      this.code = "1653951718-l360kXNB"
    } else if ( this.$route.params.judge == "souls" ){
      this.code = "1653951718-R7Yj13P7"
    } else {
      this.code = "1653951718-27bRkDQG"
    }
    window.liff
      .init({
        liffId: this.code
      })
      .then(() => {
        this.getProfile();
      })
      .catch((err) => {
        console.log(err)
      })
    console.log(this.$route.params.judge)
  },
  methods: {
    changeJudge(name){
      this.judge = name;
    },
    getProfile(){
      // window.liff
      //   .getProfile()
      //   .then( profile => {
      //     alert(profile.displayName)
      //   } )
    }
  }
};
</script>