<template>
  <div id="related-2">
    <div class="banner">
      <img src="~$images/relateds-banner.jpg">
      <div class="banner-content">
        <p>{{content}}</p>
      </div>
    </div>

    <main class="content">
      <section class="content">
        <div class="search-block">
          <form>
            <div>
              <input type="text" class="related-name" v-model="searchRelatedText" placeholder="測試名稱" aria-label="Recipient's username" aria-describedby="button-addon2">
              <button class="search-btn" @click.prevent="searchRelated()">尋找關係人</button>
            </div>
          </form>
        </div>

        <div class="related-list">
          <ul id="related-list">
            <li v-for="(related, index) in allRelateds"
              :key="index"
              :class="{active: related.name === selectRelated.name, 'no-found': !related.found}"
            >
              <a @click="changeRelated(index)" v-if="related.found">
                <div>
                  <img src="~$images/user.png">
                </div>
                <p>
                  {{related.name}}
                </p>
              </a>
              <a v-else></a>
            </li>
          </ul>
        </div>

        <div v-if="found && selectRelated" class="detail">
          <div
            v-for="(testimony, index) in selectRelated.testimonies"
            :key="index"
            :class="{ block: !testimony.show }"
          >
            <h4 v-if="testimony.show"> Q: {{ testimony.title }}</h4>
            <p v-if="testimony.show" class="detail-list" v-html="testimony.content">
               
            </p>
            <p v-if="!testimony.show">
              找到關鍵證物解鎖證詞
            </p>
          </div>
        </div>
        <div v-else>
          查無此人
        </div>

      </section>

    </main>
  </div>
