<script>
import axios from 'axios';

export default {
  data() {
    return {
      judge: this.$route.params.judge,
      content: this.sampleContnet(),
      allRelateds: "",
      group_id: this.$route.query.group_id,
      story_id: "",
      line_id: "",
      victim: "",
      selectRelated: null,
      searchRelatedText: "",
      newRelated: false,
      found: true
    };
  },
  beforeMount(){
    axios.get("https://judicial-intern.dmmt.design/api/v1/line/relateds?group_id=" + this.group_id)
    .then(response => {
      this.init(response.data)
    }).catch(error=> {
      console.log(error)
    })
  },
  mounted(){
    console.log(this.$route.params.judge)
    console.log(this.$route.query.group_id)
  },
  methods: {
    init(data){
      this.group_id = data.group.id
      this.line_id = data.group.line_id
      this.victim = data.relateds.victim.name
      this.allRelateds = data.relateds.all
      this.story_id = data.group.story_id
      this.selectRelated = this.allRelateds[0]
    },
    searchRelated(){
      axios.get("https://judicial-intern.dmmt.design/api/v1/line/related/" + this.searchRelatedText,
        { params: { group_id: this.group_id, story_id: this.story_id } }
      )
      .then(response => {
        this.setSearchResult(response.data)
      }).catch(error=> {
        console.log(error)
      })
    },
    setSearchResult(data){
      this.found = data.found
      this.allRelateds = data.relateds.all
      if (this.found){
        this.selectRelated = this.allRelateds.find( related => related.name == data.relateds.related.name) 
      }
    },
