<template>
  <div id="exhibit-2">
    <div class="banner">
      <img src="~$images/exhibits-banner.jpg">
      <div class="banner-content">
        <p>{{content}}</p>
      </div>
    </div>

    <main class="content">
      <section class="content">
        <div class="search-block">
          <form action="/api/v1/related" method="post">
            <div>
              <input type="text" class="related-name" v-model="searchExhibitText" placeholder="證物鑑定" aria-label="Recipient's username" aria-describedby="button-addon2">
              <button class="search-btn" @click.prevent="searchExhibit()">證物鑑定</button>
            </div>
          </form>
        </div>
        <div class="process"><img src="~$images/search.svg"><span><b>{{foundTotalExhibitNum}}</b>/{{totalExhibitNum}}</span></div>
        <div class="exhibit-list" :class="{ reverse: selectExhibitCate == 'found' }">
          <ul id="default-exhibit-list" :class="{ active: selectExhibitCate == 'default', have_found: foundExhibits.length > 0  }">
            <li v-for="(exhibit) in defaultExhibits"
              :key="exhibit.tag"
              :class="{active: exhibit.tag === selectExhibit.tag}"
            >
              <a @click="setSelectExhibit('default', exhibit.tag)">
                {{ exhibit.name }}
              </a>
            </li>
          </ul>

          <ul id="found-exhibit-list" :class="{ active: selectExhibitCate == 'found' }">
            <li v-for="(exhibit) in foundExhibits"
              :key="exhibit.tag"
              :class="{active: exhibit.tag === selectExhibit.tag}"
            >
              <a @click="setSelectExhibit('found', exhibit.tag)">
                {{ exhibit.name }}
              </a>
            </li>
          </ul>
        </div>

        <div class="detail" :class="selectExhibitCate">
          <p v-if="found" id="detail">
            {{selectExhibit.report}}
          </p>
          <p v-else id="detail">
            查無此物
          </p>
        </div>
      </section>
    </main>
  </div>
</template>
<script>
import axios from 'axios';

export default {
  // components: {
  //   Nav,
  //   Relateds
  // },
  data() {
    return {
      judge: this.$route.params.judge,
      content: this.sampleContnet(),
      defaultExhibits: "",
      foundExhibits: "",
      selectExhibit: "",
      group_id: this.$route.query.group_id,
      story_id: "",
      line_id: "",
      selectExhibitCate: "default",
      searchExhibitText: "",
      found: true,
      totalExhibitNum: "",
      foundTotalExhibitNum: "",
    };
  },
  beforeMount(){
    axios.get("https://judicial-intern.dmmt.design/api/v1/line/exhibits?group_id=" + this.group_id)
    .then(response => {
      this.init(response.data)
    }).catch(error=> {
      console.log(error)
    })
  },
  mounted(){
    console.log(this.$route.params.judge)
  },
  methods: {
    init(data){
      this.group_id = data.group.id
      this.line_id = data.group.line_id
      this.defaultExhibits = data.exhibits.default_exhibits
      this.foundExhibits = data.exhibits.found_exhibits
      this.story_id = data.group.story_id
      this.selectExhibit = this.defaultExhibits[0]
      this.totalExhibitNum = data.exhibits.total_num
      this.foundTotalExhibitNum = data.exhibits.found_total_num
    },
    searchExhibit(){
      axios.get("https://judicial-intern.dmmt.design/api/v1/line/exhibit/" + this.searchExhibitText,
        { params: { group_id: this.group_id, story_id: this.story_id } }
      )
      .then(response => {
        this.setSearchResult(response.data)
      }).catch(error=> {
        console.log(error)
      })
    },
    setSearchResult(data){
      this.foundExhibits = data.exhibits.found_exhibits
      this.found = data.found
      if (this.found){
        this.selectExhibit = data.exhibits.exhibit
        this.foundTotalExhibitNum = data.exhibits.found_total_num
        this.selectExhibitCate = this.selectExhibit.status
        if ( data.exhibits.update_list ){
          if ( data.unlock_position ){
            window.liff.sendMessages([{
              type: 'text',
              text: "我發現關鍵證物了，快去找靈陽查看新地點"
            }]);
          }
          if ( data.unlock_testimony ){
            window.liff.sendMessages([{
              type: 'text',
              text: "我發現關鍵證物了，快去找冥鏡查看新證詞"
            }]);
          }
          if ( data.finish ){
            window.liff.sendMessages([{
              type: 'text',
              text: "我發現全部證物了！！"
            }]);
          }
        }
      }
    },
    sampleContnet(){
      let contents = ["呵欠……你不覺得調查工作很無聊嗎？不如跟我一起去摸魚吧。",
            "這種簡單的小情報，花兩分鐘就調查出來了，剩下的就交給你囉。",
            "你問我怎麼從別人口中套話？嗯，多多去跟那些鬼吏大媽串門子吧。"]
      return contents[Math.floor(Math.random() * 3)]
    },
    setDefaultExhibits(){
      let defaultExhibits = [
        { 
          tag: "shang_haiex1",
          name: "命案現場",
          status: "default",
          report: "命案現場位在上海旅社二樓樓梯右側二一四號房，旁邊就是大廳，大廳有個櫃檯，晚上過了十點就會把樓下的鐵門拉上，值班人員也就去休息了。但那天吳國全被姚經理叫醒弄份宵夜後，就一直趴在值班台睡覺。\u2028\u2028房間內，死者被麻繩吊死在樑上，木椅橫躺在腳下。桌上有兩瓶空的高粱酒、兩個指紋挺殘留的酒杯、散落一桌的花生殼、四道空盤、還有一份當天的早報。\u2028\u2028房間沒有任何打鬥痕跡，除吳碩所說一千菲律賓幣不見外，無任何金錢遺失。\u2028死者口袋裡找到一張一千塊台幣的借條。"
        }, {
          tag: "shang_haiex2",
          name: "檢驗報告",
          status: "default",
          report: "死者姓名：姚季桓\r\n    性別：男\r\n    年齡：65歲\r\n    法醫：蕭修平\r\n    屍體之心臟血液內、肝、腎、脾、腦、腹部肌肉、尿水等，化驗出有巴拉松毒物存在。左上腹部有疑似針孔的傷口，推測生前被針管插入左上腹部血管，注入巴拉松毒液進入進體內。"
        }, {
          tag: "shang_haiex3",
          name: "借條",
          status: "default",
          report: "一張一千塊台幣借條，經手人是林聖組。"
        }]
      return defaultExhibits
    },
    setSelectExhibit(cate, tag){
      this.found = true
      this.selectExhibitCate = cate
      if (cate == "default"){
        this.selectExhibit = this.defaultExhibits.find(  exhibit => exhibit.tag == tag )
      } else{
        this.selectExhibit = this.foundExhibits.find(  exhibit => exhibit.tag == tag )
      }
    },
    setFoundExhibits(){
      let foundExhibits = [
        {
          tag: "shang_haiex4",
          name: "早報",
          status: "found",
          report: "報紙斗大標題寫著『華僑姚季桓提告上海旅社老闆黃祥博財務訴訟案敗訴。』"
        }, {
          tag: "shang_haiex5",
          name: "指紋",
          status: "found",
          report: "酒杯上的指紋，除了死者與吳碩外，還有一枚吳國全的指紋。"
        }, {
          tag: "shang_haiex6",
          name: "檢舉信",
          status: "found",
          report: "這是一封匿名信，信上寫著『法醫葉奕丞收取上海旅社黃祥博五萬塊現金，希望對姚季桓的驗屍報告作假，把死亡因素偽造成上吊自殺。』仔細看這是一封左撇子寫得檢舉信。"
        }, {
          tag: "shang_haiex7",
          name: "驗屍報告",
          status: "found",
          report: "死者姓名：姚季桓\r\n    性別：男\r\n    年齡：65歲\r\n    法醫：葉奕丞\r\n    死者身體除脖子勒痕，以及脖子因為求生本能所造成的指甲割痕外，無其他外傷。舌頭也因上吊自殺，舌頭吐在外頭，嘴裡仍然殘留高粱的味道。體內血液檢測出大量酒精，依照命案現場推測死前有大量飲酒。身體無任何打鬥痕跡、推測死因應為自殺。"
        }, {
          tag: "shang_haiex8",
          name: "麻繩",
          status: "found",
          report: "在倉庫內找到與死者上吊相同的麻繩。"
        }, {
          tag: "shang_haiex9",
          name: "手套",
          status: "found",
          report: "在死者的麻繩上找到與手套一樣的纖維。"
        }, {
          tag: "shang_haiex10",
          name: "五百元菲律賓幣",
          status: "found",
          report: "在吳國全的枕頭裡底找到這五百元。"
        }, {
          tag: "shang_haiex11",
          name: "遺書",
          status: "found",
          report: "在吳國全的衣櫥裡找到一封死者的遺書。信上寫著『姚某無能無才，跟江東父老借了這麼多錢來台投資，最後卻投資失利，已無臉回鄉，希望大家能原諒的話。仔細看這是一封左手字跡的遺書。"
        }, {
          tag: "shang_haiex12",
          name: "針管",
          status: "found",
          report: "根據驗屍報告推測集體內殘留的巴拉松劑量，猜測應該是5ML的玻璃針管。"
        }, {
          tag: "shang_haiex13",
          name: "瓶子",
          status: "found",
          report: "在林聖祖家院子裡挖到一個標示『灰錳氧』的瓶子，裡面檢驗出有巴拉松的成分。瓶子外有台灣大學的標誌。"
        }, {
          tag: "shang_haiex14",
          name: "保險庫",
          status: "found",
          report: "保險庫內被撬開，裡面空無一物。"
        }, {
          tag: "shang_haiex15",
          name: "帳本",
          status: "found",
          report: "清查帳本發現保險庫裡遺失了台幣現金二十五萬、美金十萬、三塊十兩標示中央造幣廠製的金條三塊、金戒指五個、所有權狀一只。"
        }, {
          tag: "shang_haiex16",
          name: "借條",
          status: "found",
          report: "一二十張的借條，每張借條大概都是一兩百台幣，借款人都是姚季桓。"
        }, {
          tag: "shang_haiex17",
          name: "皮夾",
          status: "found",
          report: "皮夾內有五百塊菲律賓幣、三百塊台幣、一張身分證、傳票。"
        }
      ]
      return foundExhibits
    }
  }
};
</script>

